*{
    padding:0;
    margin:0;
    box-sizing:border-box;
}

.nav ul{
    background-color: rgb(248, 194, 194);  
    display:flex;
    flex-direction: row;
    justify-content: space-between;
}
ul li{
    list-style: none;
    text-decoration: none;
    margin:25px;
}
.user{
    display:flex;
    flex-direction: row;
    justify-content:end;
}
.cta{
    border:2px solid #fff; 
    border-radius:5px;
    overflow: hidden;
    font-size: large;
}
.login{
    width:50px; 
}

.register{
    height:28px;
    width:65px;
}
.btn{
    width:85px;
    height:25px;
    border:5px solid white;
    border-radius:10px;
    font-size: large;
    color:black;
    cursor:pointer;
    background-color:white;
}
.addNew{
    margin-bottom:10px;
}